"use client"

import { useState } from "react";
import api from "../../../lib/axios";
import { useRouter } from "next/navigation";

export default function CreatePostPage() {
  const [form, setForm] = useState({ title: "", isi: "" });
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const handleChange = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
  e.preventDefault();
  setLoading(true);

  // ambil token dari localStorage atau context
  const token = localStorage.getItem("token"); 
  console.log(token);

  try {
    const res = await fetch("/api/proxy/posts", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${token}`,
      },
      body: JSON.stringify(form),
    });

    if (!res.ok) throw new Error("Failed");
    router.push("/posts");
  } catch (err) {
    alert(err.message);
  }

  setLoading(false);
};


  return (
    <div className="flex items-center justify-center min-h-screen">
      <div className="card w-96 bg-base-100 shadow-xl p-6">
        <h2 className="card-title mb-4">Create Post</h2>
        <form onSubmit={handleSubmit} className="flex flex-col gap-3">
          <input type="text" name="title" placeholder="Title" className="input input-bordered" onChange={handleChange} />
          <textarea name="isi" placeholder="Isi" className="textarea textarea-bordered" onChange={handleChange}></textarea>
          <button type="submit" className={`btn btn-primary ${loading ? "loading" : ""}`}>Create</button>
        </form>
      </div>
    </div>
  );
}
